/* General Styling */
body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f9f9f9;
    color: #333;
    line-height: 1.6;
    overflow-x: hidden;
}

/* Navbar Styling */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(45deg, #5196c7, #ffe869);
    padding: 15px 30px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
    transition: background 0.5s ease, box-shadow 0.5s ease;
}

.navbar:hover {
    background: linear-gradient(45deg, #ffe869, #5196c7);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.logo-container {
    display: flex;
    align-items: center;
}

.logo {
    height: 60px;
    width: 50px;
    margin-right: 15px;
}

.text-container h1 {
    font-size: 1.8em;
    font-weight: bold;
    margin: 0;
    color: white;
}

.text-container p {
    font-size: 1em;
    margin: 0;
    color: white;
}

/* Hamburger Menu */
.hamburger {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 1100;
    outline: none;
}

.hamburger .bar {
    width: 25px;
    height: 3px;
    background: white;
    margin: 3px 0;
    transition: all 0.3s ease-in-out;
}

.hamburger.open .bar:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.hamburger.open .bar:nth-child(2) {
    opacity: 0;
}

.hamburger.open .bar:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
}

/* Navigation Links */
.nav-links {
    display: flex;
    align-items: center;
    transition: all 0.5s ease;
}

.nav-links ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

.nav-links ul li {
    margin-left: 25px;
}

.nav-links ul li a {
    text-decoration: none;
    color: white;
    font-size: 1em;
    padding: 10px 15px;
    transition: background 0.3s ease, color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
    border-radius: 5px;
}

.nav-links ul li a:hover {
    background-color: rgb(3, 110, 198);
    color: #f3ff10;
    transform: scale(1.2);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
}

/* Mobile Styling */
@media (max-width: 768px) {
    .hamburger {
        display: flex;
    }

    .nav-links {
        position: fixed;
        top: 0;
        right: -100%;
        height: 100%;
        width: 250px;
        flex-direction: column;
        background: linear-gradient(45deg, #5196c7, #ffe869);
        justify-content: center;
        align-items: center;
        transition: right 0.3s ease-in-out;
    }

    .nav-links.active {
        right: 0;
    }

    .nav-links ul {
        flex-direction: column;
        width: 100%;
    }

    .nav-links ul li {
        margin: 15px 0;
    }

    .nav-links ul li a {
        width: 100%;
        text-align: center;
    }
}